/** Media Queries **/
@mixin tipografia($tipo, $tamanio, $color, $grosor) {
    font-size: $tamanio;
    color: $color;
    font-family: $tipo;
    font-weight: $grosor;
}

@mixin media400 {
    @media (min-width: 400px) {
        @content;
    }
}

@mixin media500 {
    @media (min-width: 500px) {
        @content;
    }
}

