// estilo
// Needs latest Compass, add '@import "compass"' to your scss
// Permalink - use to edit and share this gradient:
// https://colorzilla.com/gradient-editor/#64b8ff+0,54c8e5+46,64b8ff+100
@mixin degragado-azul {
    /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#64b8ff+0,54c8e5+46,64b8ff+100 */
    background: rgb(100,184,255); /* Old browsers */
    background: -moz-linear-gradient(-45deg,  rgba(100,184,255,1) 0%, rgba(84,200,229,1) 46%, rgba(100,184,255,1) 100%); /* FF3.6-15 */
    background: -webkit-linear-gradient(-45deg,  rgba(100,184,255,1) 0%,rgba(84,200,229,1) 46%,rgba(100,184,255,1) 100%); /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(135deg,  rgba(100,184,255,1) 0%,rgba(84,200,229,1) 46%,rgba(100,184,255,1) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#64b8ff', endColorstr='#64b8ff',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */

}

@mixin degragado-video {
    /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#43cee0+0,b7deed+40,b7deed+62,43cee0+100&0.5+0,0.5+100 */
    background: -moz-linear-gradient(45deg,  rgba(67,206,224,0.5) 0%, rgba(183,222,237,0.5) 40%, rgba(183,222,237,0.5) 62%, rgba(67,206,224,0.5) 100%); /* FF3.6-15 */
    background: -webkit-linear-gradient(45deg,  rgba(67,206,224,0.5) 0%,rgba(183,222,237,0.5) 40%,rgba(183,222,237,0.5) 62%,rgba(67,206,224,0.5) 100%); /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(45deg,  rgba(67,206,224,0.5) 0%,rgba(183,222,237,0.5) 40%,rgba(183,222,237,0.5) 62%,rgba(67,206,224,0.5) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#8043cee0', endColorstr='#8043cee0',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
}